// 自定义变量
//颜色
$body-bg:#f4f5f5;
$white: #fff;
$primary:#1e80ff;
$success: #6cbd45;


$text-color:#909090;
$text-search:#666;
$icon-color:#b2bac2;
$border-color:#f1f1f1;
$box-shadow: rgb(0 0 0 / 5%);
$typehead-color:#869aab; //搜索记录
$typehead-list-color:#5e6369; //搜索记录
$title:#2e3135;



// 大小
$header-h: 61px;
$header-btn-h:32px;
$nav-h:45px;
$main-top:15px;
// 全局混入

//单行文本溢出
@mixin nowrap {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

//指定行数溢出
@mixin clamp($num) {
  display: -webkit-box;
  -webkit-line-clamp: $num;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

//禁止选中
@mixin no-select {
  -moz-user-select: none;
  /*火狐*/
  -webkit-user-select: none;
  /*webkit浏览器*/
  -ms-user-select: none;
  /*IE10*/
  -khtml-user-select: none;
  /*早期浏览器*/
  -o-user-select: none;
  /* Opera*/
  user-select: none;
}


// 图片
@mixin imgFit($fit:cover) {
  width: 100%;
  height: 100%;
  object-fit: $fit;
  vertical-align: top;
}

//去除元素移动端点击的背景色
@mixin no-highlight {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: transparent;
}
// 全局方法
// 重置样式
:root {
  box-sizing: border-box;
  font-family: system-ui,
    -apple-system,
    BlinkMacSystemFont,
    segoe ui,
    Roboto,
    Helvetica,
    Arial,
    sans-serif;
}

*,
::before,
::after {
  box-sizing: inherit;
  font-family: inherit;
}

html,
body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #333;
  background-color: $body-bg;
}

body {
  overflow-y: scroll;
}

.container {
  max-width: 960px;
  margin: auto;

  @media screen and (max-width:980px) {
    width: 96%;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}


// 去除谷歌自动填充input样式-颜色覆盖
input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px white inset;
}

//通知自定义字符图标
.notyf-icon {
  font-size: 21px;
}


//折叠动画
.mu-category-collapse {
  transition: height .25s;
}
//header
@import "@/assets/scss/default/header.scss";
@import "@/assets/scss/default/nav.scss";
@import "@/assets/scss/default/login-dialog.scss";
@import "@/assets/scss/default/fixed-tool.scss";


#main {
  padding-top: calc(#{$header-h} + #{$nav-h} + #{$main-top});

  .content {
    position: relative;

    .left {
      width: 700px;
      border-radius: 2px;
      background-color: $white;
      margin-right: 260px;

      @media screen and (max-width:960px) {
        width: 100%;
        margin-right: 0;
      }

      // 快捷导航
      .archive-title {
        padding: 16px 12px;
        border-bottom: 1px solid $border-color;

        .title {
          font-size: 14px;
          font-weight: normal;
          color: $text-color;

          strong {
            font-size: 18px;
            font-weight: bold;
            color: $primary;
          }
        }
      }

      //骨架屏
      @import "@/assets/scss/index/skeleton";

      // 文章列表
      #article-list-wrap {
        &.hide {
          height: 0;
          display: none;
          overflow: hidden;
        }

        @import "@/assets/scss/index/mini-article-card";

        //无内容占位
        .archive-placeholder {
          padding: 80px 50px;
          text-align: center;

          img {
            width: 100%;
            vertical-align: top;
          }

          @media screen and (max-width:420px) {
            padding: 20px 15px;
          }
        }
      }



      //自动加载
      @import "@/assets/scss/index/auto-load";
    }

    .right {
      position: absolute;
      top: 0;
      right: 0;
      width: 240px;

      @media screen and (max-width:960px) {
        display: none;
      }

      // 最近评论
      .recent-comment {
        background-color: $white;
        margin-bottom: 16px;
        box-shadow: 0 1px 2px 0 $box-shadow;

        .recent-head {
          padding: 12px 16px;
          font-size: 14px;
          line-height: 1;
          color: $title;
          display: flex;
          align-items: center;
          border-bottom: 1px solid $border-color;

          .icon {
            font-size: 12px;
            color: $icon-color;
            margin-right: 5px;
          }
        }

        .recent-body {
          .item {
            .link {
              display: flex;
              align-items: center;
              color: inherit;
              text-decoration: none;
              padding: 12px 16px;

              &:hover,
              &:active {
                background-color: darken($white, 2%);
              }
            }

            .comment-avatar {
              flex-shrink: 0;
              width: 45px;
              height: 45px;
              border-radius: 50%;
              overflow: hidden;
              margin-right: 6px;

              img {
                @include imgFit();
              }
            }

            .user-info {
              min-width: 0;
              flex-grow: 1;

              .title {
                font-size: 14px;
                font-weight: normal;
                color: $title;
                margin-bottom: 3px;
                @include nowrap();
              }

              .subtitle {
                font-size: 12px;
                color: $icon-color;
                word-wrap: break-word;
                @include nowrap();

                img {
                  width: 20px;
                  height: 20px;
                  vertical-align: sub;
                  margin-left: 4px;

                  &+img {
                    margin-right: 2px;
                  }
                }
              }
            }
          }
        }

        .recent-footer {
          padding: 12px 0;
          display: flex;
          justify-content: center;
          align-items: center;
          color: $primary;
          border-top: 1px solid $border-color;

          a {
            font-size: 14px;
            color: inherit;
            text-decoration: none;
          }

          .icon {
            font-size: 14px;
            margin-left: 5px;
          }
        }
      }

      // 主题小工具
      .theme-tool {
        background-color: $white;
        margin-bottom: 16px;
        box-shadow: 0 1px 2px 0 $box-shadow;

        .item {
          .link {
            color: inherit;
            text-decoration: none;
            display: flex;
            align-items: center;
            padding: 12px;

            &:hover,
            &:active {
              background-color: darken($white, 2%);
            }

            .tool-pic {
              flex-shrink: 0;
              width: 36px;
              height: 36px;
              margin-right: 10px;

              img {
                @include imgFit(contain);
              }
            }

            .title {
              min-width: 0;
              flex-grow: 1;
              font-size: 14px;
              font-weight: normal;
              color: $title;
              line-height: 1;
              @include nowrap();
            }
          }
        }
      }

      //footer
      @import "@/assets/scss/default/footer";

    }
  }

}